@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    background-color: v.$primary;
    padding: 7rem 0 5rem 0;

    @include m.tablet {
        padding: 12rem 0 9rem 0;
    }

    &__container {
        width: 80%;
        max-width: 1440px;
        margin: 0 auto;

        @include m.tablet {
            width: 87%;
        }
    }

    &__brand {
        width: 18rem;
        margin-bottom: 4rem;

        @include m.tablet {
            width: 20rem;
            margin-bottom: 3rem;
        }
    }

    &__grid {
        display: grid;
        gap: 6rem;

        @include m.desktop {
            grid-template-columns: 30% 20% 20% 30%;
        }
    }

    &__box {
        &:nth-child(2),
        &:nth-child(3) {
            display: grid;
            gap: 2rem;
        }
        &:nth-child(4) {
            display: flex;
            justify-content: center;

            @include m.tablet {
                justify-content: flex-start;
                align-items: flex-start;
            }   
        }
    }
    
    &__contact {
        color: v.$white;
        
        @include m.tablet {
            font-size: 1.8rem;
        }
        
        &:before {
            content: '';
            display: inline-block;
            background-repeat: no-repeat;
            height: 2rem;
            width: 2rem;
            vertical-align: middle;
            margin-right: 2rem;
        }

        &:nth-child(1) {
            &:before {
                background-image: url(../img/icon-phone.svg);
            }
        }
        
        &:nth-child(2) {
            margin-top: 1.7rem;

            &:before {
                background-image: url(../img/icon-email.svg);
            }
        }
    }

    &__link {
        color: v.$white;

        &:hover {
            color: v.$accentBlue;
        }

        @include m.tablet {
            font-size: 1.8rem;
        }
    }
}

.sociales {
    display: flex;
    gap: 1.5rem;
    
    &__link {
        padding: .6rem;
        border: 1px solid v.$white;
        border-radius: 9rem;

        &:hover {
            filter: invert(44%) sepia(86%) saturate(7010%) hue-rotate(211deg) brightness(103%) contrast(110%);
        }

        @include m.tablet {
            padding: .8rem;
        }
    }

    &__link::before {
        content: '';
        padding: .7rem;
        display: block;
        background-repeat: no-repeat;
        
        @include m.tablet {
            padding: .8rem;
        }
    }
    
    &__link[href*="facebook.com"]::before {
        background-image: url(../img/rrss-facebook.svg);
    }
    &__link[href*="twitter.com"]::before {
        background-image: url(../img/rrss-twitter.svg);
    }
    &__link[href*="instagram.com"]::before {
        background-image: url(../img/rrss-instagram.svg);
    }
    
    &__accesible { 
        display: block;
        width: 1px;
        height: 1px;
        margin: 0;
        border: 0;
        padding: 0;
        overflow: hidden;
        clip: rect(0,0,0,0);
    }
}

.attribution {
    display: flex;
    justify-content: center;
    padding: .8rem 0;
    background-color: #070439;
    border-top: 1px solid #2d2d58;

    &__grid {
        display: flex;
        gap: 1.5rem;
    }
    
    &__text,
    &__link {
        font-size: 1.2rem;
        color: #343466;
    }

    &__link {
        &:hover {
            color: v.$accentBlue;
        }
    }
}