@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    padding-top: 2.5rem;

    @include m.tablet {
        padding-top: 7rem;
    }

    &__navbar {
        display: flex;
        justify-content: space-between;
    }

    &__brand {
        width: 8.5rem;

        @include m.tablet {
            width: 13.5rem;
        }

        @include m.desktopxl {
            width: 18.5rem;
        }
    }

    &__grid {
        margin-top: 10rem;

        @include m.tablet {
            display: grid;
            grid-template-columns: 55% 45%;
            gap: 3rem;
            align-items: center;
        }

        @include m.desktopxl {
            grid-template-columns: 48% 52%;
        }
    }

    &__image {
        width: 93%;
        margin: 0 auto 4rem auto;

        @include m.phone {
            width: 75%;
        }

        @include m.tablet {
            width: 100%;
            margin: 0;
        }
    }
    
    &__heading {
        font-family: v.$mainFont;
        font-weight: 700;
        line-height: 1.5;
        text-align: center;
        color: v.$primary;

        @include m.tablet {
            font-size: 3rem;
            line-height: 1.2;
            text-align: left;
        }

        @include m.desktopxl {
            font-size: 4.4rem;
            line-height: 1.5;
        }
    }

    &__content {
        @include m.tablet {
            order: -1;
        }

        @include m.desktopxl {
            margin-top: -7rem;
        }
    }

    &__description {
        width: 92%;
        margin: 2.2rem auto 0 auto;

        @include m.tablet {
            margin: 2.2rem 0 0 0;
        }
    }

    &__text {
        @include m.paragraph;
    }

    &__form {
        margin-top: 3rem;

        @include m.tablet {
            display: grid;
            grid-template-columns: 62% 38%;
            gap: 1.5rem;
            width: 92%;
        }
    }

    &__input{
        padding: .8rem 2rem;
        width: 100%;
        border-radius: .4rem;
        border: .2rem solid hsla(240, 22%, 44%, 0.7);

        &::placeholder {
            font-size: 1.2rem;
            font-family: v.$secondaryFont;
            letter-spacing: .05rem;
            color: v.$neutralGray;
        }

        @include m.tablet {
            padding: 1rem 2rem;

            &::placeholder {
                font-size: 1.4rem;
            }
        }

        @include m.desktopxl {
            padding: 1.2rem 2rem;

            &::placeholder {
                font-size: 1.6rem;
            }
        }
    }

    &__submit {
        margin-top: 1.5rem;
        padding: 1.2rem 2rem;
        width: 100%;
        font-family: v.$secondaryFont;
        font-size: 1.4rem;
        font-weight: 700;
        background-color: v.$accentBlue;
        border: none;
        border-radius: .4rem;
        color: v.$white;
        box-shadow: 6px 6px 3px -3px rgba(231,231,231,1);
        cursor: pointer;

        @include m.tablet {
            margin-top: 0;
        }
    }
}

.menu {
    display: flex;
    align-items: center;
    gap: 2.5rem;

    @include m.tablet {
        gap: 4.25rem;
    }

    @include m.desktopxl {
        gap: 6rem;
    }
    
    &__link {
        font-size: 1.25rem;
        font-family: v.$secondaryFont;
        color: v.$primary;

        &:hover {
            color: v.$accentBlue;
        }

        @include m.tablet {
            font-size: 1.6rem;
        }

        @include m.desktopxl{
            font-size: 1.9rem;
        }
    }
}

input:focus {
    outline: none;
    border: .2rem solid hsl(216, 100%, 50%);
}
