@use 'base/variables' as v;
@use 'base/mixins' as m;

.subscribe {
    padding: 5rem 0 7rem 0;
    background-color: v.$secondary;

    @include m.tablet {
        padding: 10rem 0 12rem 0;
    }

    &__grid {
        @include m.tablet {
            display: grid;
            grid-template-columns: 60% 40%;
        }
    }

    &__heading {
        @include m.heading;
        color: v.$white;
        margin-bottom: 1.8rem;

        @include m.tablet {
            margin-bottom: 2rem
        }
    }
    
    &__text {
        @include m.paragraph;
        color: v.$white;
        width: 85%;
        margin: 0 auto;

        @include m.tablet {
            margin: 0;
            max-width: 63rem;
            line-height: 1.6;
        }
    }

    &__form {
        width: 75%;
        margin: 2.5rem auto 0 auto;

        @include m.tablet {
            width: 100%;
            margin: 1rem 0 0 0;
        }
    }

    &__input{
        padding: .6rem 1rem;
        width: 100%;
        border-radius: .4rem;
        border: .2rem solid hsl(239, 22%, 41%);

        &::placeholder {
            font-size: 1rem;
            font-family: v.$secondaryFont;
            color: v.$neutralGray;
        }

        @include m.tablet {
            padding: 1rem 2rem;

            &::placeholder {
                font-size: 1.4rem;
            }
        }

        @include m.desktopxl {
            padding: 1.6rem 2rem;

            &::placeholder {
                font-size: 1.6rem;
            }
        }
    }

    &__submit {
        margin-top: .8rem;
        padding: 1.2rem 2rem;
        width: 100%;
        font-family: v.$secondaryFont;
        font-size: 1rem;
        font-weight: 700;
        letter-spacing: .1rem;
        background-color: v.$accentBlue;
        border-radius: .2rem;
        border: .1rem solid v.$secondary;
        color: v.$white;
        box-shadow: 6px 6px 3px -3px rgba(68, 68, 68, 0.25);
        cursor: pointer;

        @include m.tablet {
            width: auto;
            margin-top: 1.1rem;
            padding: 1.6rem 2.75rem;
            font-size: 1.2rem;
            border-radius: .4rem;
        }

        @include m.desktop {
            margin-top: 1.4rem;
            padding: 2rem 3.5rem;
            font-size: 1.4rem;
            border-radius: .4rem;
        }
    }
}

