}
@media (min-width: 768px) {
  .footer__link {
    font-size: 1.8rem;
  }
}

.sociales {
  display: flex;
  gap: 1.5rem;
}
.sociales__link {
  padding: 0.6rem;
  border: 1px solid #ffffff;
  border-radius: 9rem;
}
.sociales__link:hover {
  filter: invert(44%) sepia(86%) saturate(7010%) hue-rotate(211deg) brightness(103%) contrast(110%);
}
@media (min-width: 768px) {
  .sociales__link {
    padding: 0.8rem;
  }
}
.sociales__link::before {
  content: "";
  padding: 0.7rem;
  display: block;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .sociales__link::before {
    padding: 0.8rem;
  }
}
.sociales__link[href*="facebook.com"]::before {
  background-image: url(../img/rrss-facebook.svg);
}
.sociales__link[href*="twitter.com"]::before {
  background-image: url(../img/rrss-twitter.svg);
}
.sociales__link[href*="instagram.com"]::before {
  background-image: url(../img/rrss-instagram.svg);
}
.sociales__accesible {
  display: block;
  width: 1px;
  height: 1px;
  margin: 0;
  border: 0;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
}

.attribution {
  display: flex;
  justify-content: center;
  padding: 0.8rem 0;
  background-color: #070439;
  border-top: 1px solid #2d2d58;
}
.attribution__grid {
  display: flex;
  gap: 1.5rem;
}
.attribution__text, .attribution__link {
  font-size: 1.2rem;
  color: #343466;
}
.attribution__link:hover {
  color: hsl(216deg, 100%, 50%);
}