@use 'variables' as v;

/** Media Queries **/
@mixin phone {
    @media (min-width: v.$phone) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin desktopxl {
    @media (min-width: v.$desktopxl) {
        @content;
    }
}

@mixin desktopxxl {
    @media (min-width: v.$desktopxxl) {
        @content;
    }
}

@mixin ultra {
    @media (min-width: v.$ultra) {
        @content;
    }
}

/* Grid */
@mixin grid($columns, $spacing ) {
    display: grid;
    gap: $spacing;
    grid-template-columns: repeat($columns, 1fr);
}

/* Headings and paragraphs */
@mixin heading {
        font-family: v.$secondaryFont;
        font-size: 2.5rem;
        font-weight: 700;
        text-align: center;
        color: v.$primary;

        @include tablet {
            font-size: 3rem;
            text-align: left;
        }

        @include desktopxl {
            font-size: 3.6rem;
        }
}

@mixin paragraph {
    font-size: 1.45rem;
    color: v.$primary;
    text-align: center;

    @include tablet {
        font-size: 1.35rem;
        text-align: left;
        line-height: 1.5;
    }

    @include desktop {
        font-size: 1.7rem;
        line-height: 1.3;
    }

    @include desktopxl {
        font-size: 2rem;
    }
}