@use 'base/variables' as v;
@use 'base/mixins' as m;

.cloud {
    margin-top: 9rem;

    @include m.tablet {
        margin-top: 12rem;
    }
    
    &__wave {
        width: 100%;
    }

    &__background {
        background-color: #f6f6fe;
        padding: 3rem 0 5rem 0;

        @include m.tablet {
            padding-bottom: 9rem;
        }
    }

    &__grid {
        padding: 2rem 0;

        @include m.tablet {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 4rem;
        }
    }

    &__content {
        margin-top: 9rem;

        @include m.tablet {
            margin-top: 0;
            order: -1;
        }
    }

    &__heading {
        @include m.heading;
        margin-bottom: 2.8rem;

        @include m.tablet {
            margin-bottom: 3.4rem;
        }
    }

    &__text {
        font-size: 1.45rem;
        color: #7d768a;

        &:nth-child(3) {
            margin-top: 2rem;
        }

        @include m.tablet {
            font-size: 1.35rem;
            text-align: left;
            line-height: 1.5;
            color: v.$primary;
        }

        @include m.desktop {
            font-size: 1.75rem;
        }

        @include m.desktopxl {
            max-width: 60rem;
            font-size: 1.8rem;
        }
    }

    &__btn {
        margin: 3.6rem 0 4rem 0;
        display: flex;
        justify-content: center;

        @include m.tablet {
            margin: 2rem 0 4.4rem 0;
            justify-content: flex-start;
        }

        @include m.desktopxl {
            margin: 2rem 0 4.8rem 0;
        }
    }

    &__link {
        padding-bottom: .5rem;
        border-bottom: .1rem solid v.$accentCyan;
        font-size: 1.4rem;
        color: v.$accentCyan;
        
        &::after {
            content: '';
            display: inline-block;
            background-image: url(../img/icon-arrow.svg);
            background-repeat: no-repeat;
            background-size: 1.7rem;
            height: 1.7rem;
            width: 1.7rem;
            vertical-align: middle;
            margin-left: .8rem;
        }

        &:hover {
            filter: saturate(250%);
        }

        @include m.desktopxl {
            font-size: 1.8rem;

            &::after {
                background-size: 2rem;
                height: 2rem;
                width: 2rem;
            }
        }
    }
}

.testimonial {
    margin: 0 2.4rem;
    padding: 2.4rem;
    border-radius: .5rem;
    background-color: v.$white;
    box-shadow: 0px 0px 16px -2px rgba(0,0,0,0.10);

    @include m.tablet {
        margin: 0;
        padding: 2.8rem 3.5rem;
        max-width: 40rem;
    }

    &__blockquote {
        margin: 0;
        font-size: 1.1rem;
        line-height: 1.6;
        color: v.$primary;

        &::before {
            content: '';
            display: block;
            background-image: url(../img/icon-quotes.svg);
            background-repeat: no-repeat;
            background-size: 1.2rem;
            margin-bottom: .6rem;
            width: 1.2rem;
            height: 1.2rem;
        }

        @include m.tablet {
            font-size: 1.35rem;
            line-height: 1.8;

            &::before {
                background-size: 1.5rem;
                margin-bottom: .6rem;
                width: 1.7rem;
                height: 1.7rem;
            }
        }

        @include m.desktopxl {
            font-size: 1.6rem;
            
            &::before {
                background-size: 1.8rem;
                margin-bottom: .6rem;
                width: 2rem;
                height: 2rem;
            }
        }
    }

    &__footer {
        margin-top: 1.5rem;
        display: flex;
        align-items: center;
        gap: 1rem;
    }

    &__box-image {
        width: 2.5rem;
        height: 2.5rem;
        border-radius: 10rem;
        overflow: hidden;

        @include m.tablet {
            width: 3.5rem;
            height: 3.5rem;
        }
    }

    &__name {
        font-size: 1rem;
        font-weight: 700;
        color: v.$primary;
        line-height: 1;

        @include m.tablet {
            font-size: 1.3rem;
        }
    }

    &__occupation {
        font-size: .7rem;
        color: v.$primary;

        @include m.tablet {
            font-size: 1rem;
        }
    }
}